.HomeHeroContainer {
  min-height: 92vh; 

  text-align: center;
  background-size: cover; 
  background-position: center;
}

.Darkshader {
  position: absolute;
  width: 100%;
  height: 92%; 
  background-color: rgba(0, 0, 0, 0.5); 
}


.TextContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}

.TextContainer h1 {
  font-size: 3rem;
}

.TextContainer p {
  font-size: 1.5rem;
}
@media (max-width: 768px) {
  .HomeHeroContainer {
    min-height: 500px; 
  }

  .Darkshader {
    height: 500px; 
  }
}
